html{
  font-size: 62.5%;
}

@media screen and (max-width: 641px){
  html{
    font-size: 50%;
  }
}

@font-face{
  font-family: "Roboto";
  src: url(../fonts/Roboto-Regular.ttf);
}

body{
  padding: 0;
  margin: 0;
  font-size: 1.6rem;
  font-family: 'Roboto';
  color: white;
  background: black;
}

select, button, input{
  background-color: inherit;
  color: inherit;
  cursor: pointer;
  border: 2px solid white;
}

menu{
  margin:0;
  padding: 0;
}

.clear__button{
  height: 100%;
}

a, a:visited{
  color: inherit;
}

a:focus{
  outline: none;
}

a:focus, a:hover{
  color: rgb(185, 121, 1);
}

option{
  background-color: black;
}

button:focus , select:focus, input:focus{
  outline: none;
  border: 2px solid rgb(185, 121, 1);
}

button{
  padding: 1rem;
  font-size: 2rem;
  border-radius: 2rem;
}

.title{
  margin: 0;
  text-transform: uppercase;
}

.icon{
  width: 4rem;
  height: 3.2rem;
}

.icon:last-child{
  margin-right: 0;
}

svg{
  width: 100% !important;
  height: 100% !important;
}

.main-container{
  max-width: 1366px;
  padding-right: 20px;
  padding-left: 20px;
  margin: 0 auto;
  min-height: 100vh;
  min-width: 400px
}

.grid-container{
  display: grid;
  grid-template-areas: 
  "header"
  "main"
  "footer";
  grid-template-columns: 100%;
  grid-template-rows: auto;
}

#header{
  grid-area: header;
}

.header{
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  margin-bottom: 3rem;
  flex-direction: column;
}

.header__title{
  font-size: 6rem;
}

.header__button{
  font-weight: bold;
  font-size: 3rem;
  border: none;
}

.header__button:focus{
  border: none;
}

#main{
  grid-area: main;
}

.main{
  display: flex;
  box-sizing: border-box;
  margin-bottom: 3rem;
  flex-direction: column;
}

.main__menu{
  margin-bottom: 3rem;
}

.menu{
  display: flex;
  font-size: 3rem;
  margin: 0;
}

.menu__item{
  margin-right: 5rem;
  cursor: pointer;
}

.menu__item:last-child{
  margin-right: 0;
}

@media screen and (min-width: 1280px){
  .services{
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 641px){
  
  .services{
    display: flex;
    margin-bottom: 3rem;
  }
  
  .form{
    margin-right: 3rem;
  }
  
  .form:last-child{
    margin-right: 0;
  }

  .form__filter{
    width: 55rem;
  }

  .form__sort{
    width: 20rem;
  }

  .form__search{
    width: 20rem;
  }
  
  .form__title{
    text-align: center;
    margin-bottom: 1.2rem;
  }

  .form__row{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
    align-items: center;
  }

  .filter__columns{
    display: flex;
  }

  .filter__column{
    margin-bottom: 0;
  }

  .filter__column:not(:last-child){
    margin-right: 2rem;
  }
  
  .column__tier{
    width: 40%;
  }

  .column__year{
    width: 60%;
  }

  .sorting__row{
    flex-direction: row;
    justify-content: center;
  }

  .sorting__list{
    margin-bottom: 1rem;
  }

  .search__row{
    margin-bottom: 0;
  }

  .form__select{
    padding: 1rem;
    border-radius: 2rem;
    margin-bottom: 2rem;
  }

  .form__button{
    width: 80%;
  }

  .form__input{
    margin-bottom: 2rem;
  }
}

@media screen and (max-width: 641px){

  .form{
    margin-bottom: 1.5rem;
  }

  .form:last-child{
    margin-bottom: 0;
  }

  .form__title{
    margin-right: 2rem;
  }
  
  .form__sort{
    margin-bottom: 1rem;
  }
  
  .form__row{
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
  }
  
  .form__row:last-child{
    margin-bottom: 0;
  }

  .form__select{
    padding: 1rem;
    border-radius: 2rem;
    border: 2px solid black;
    margin-right: 1rem;
  }

  .form__input{
    margin-right: 2rem;
  }

  .sorting__list{
    margin-right: 2rem;
  }
}

.sorting__list{
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.sorting__item{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1rem;
}

.filter__title{
  margin-bottom: 1rem;
}

.sorting__title{
  text-transform: none;
}

.sorting__item:last-child{
  margin-right: 0;
}

.input{
  padding: 1rem;
  border-radius: 2rem;
}

.input__radio{
  width: 2rem;
  height: 2rem;
}

.filter__columns{
  margin-bottom: 1rem;
}

.card-preview__list{
  display: grid;
  grid-template-columns: 18% 18% 18% 18% 20%;
  grid-template-rows: auto;
  grid-column-gap: 2%;
}

.card-preview__item{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  border: 3px solid #1889ca;
  border-radius: 2rem;
  cursor: pointer;
  margin-bottom: 3rem;
}

.card-preview__info{
  text-align: center;
  margin-top: 1rem;
}

@media screen and (min-width: 641px) and (max-width: 1365px){
  .card-preview__list{
    
    grid-template-columns: 31% 31% 33%;
    
    grid-column-gap: 2%;
  }
}

@media screen and (max-width: 641px){

  .main{
    flex-direction: column;
  }

  .services{
    width: 100%;
    margin-bottom: 2rem;
  }

  .card-preview__item{
    padding: 10px;
  }

  .card-preview__list{
    width: 100%;
    display: grid;
    grid-template-columns: 47% 47%;
    grid-template-rows: auto;
    grid-column-gap: 6%;
  }

}

.card-preview__image{
  margin-bottom: 2rem;
  max-width: 100%;
  border-radius: 2rem;
}

.card-preview__name{
  margin-bottom: 1rem;
  text-align: center;
}

.card{
  display: flex;
  width: 100%;
}

.card__left-side{
  width: 15%;
  margin-right: 6rem;
}

.card__right-side{
  margin-right: 4rem;
}

.card__image{
  text-align: center;
  margin-bottom: 2rem;
}

@media screen and (max-width: 1279px){
  .card{
    flex-direction: column;
    align-items: center;
  }

  .card__left-side{
    width: 100%;
    margin-right: 0;
    margin-bottom: 3rem;
  }

  .card__right-side{
    margin-right: 0;
    margin-bottom: 2rem;
  }
}

.card__content{
  display: flex;
}

.card__list{
  display: flex;
  flex-direction: column;
  margin-right: 2rem;
}

.card__list:last-child{
  margin-right: 0;
}

.card__list-item{
  margin-bottom: 0.5rem;
}

.card__list-item:last-child{
  margin-bottom: 0;
}

.card__list-title{
  margin-bottom: 1.5rem;
}

.card__header{
  display: flex;
  flex-direction: column;
  margin-bottom: 3rem;
}

.card__header-row{
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}

.card__header-row:last-child{
  margin-bottom: 0;
}

.card__genre{
  margin-right: 2rem;
}

.card__genre, .card__tier{
  font-size: 2.4rem;
  font-weight: bold;
}

.card__title{
  margin-right: 1.5rem;
}

.card__links{
  display: flex;
  justify-content: space-around;
}

.button__back{
  width: 9rem;
  height: 5rem;
}

@media screen and (max-width: 1279px) and (min-width: 641px){

  .card__left-side{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
  }

  .card__image{
    margin-bottom: 0;
    margin-right: 2rem;
  }
  
  .card__links{
    display: grid;
    grid-template-rows: auto auto;
    grid-template-columns: auto auto;
    grid-gap: 3rem;
  }

}

#footer{
grid-area: footer;
}

.card-preview__item{
  transition: border-color 1.2s;
}

.card-preview__item:hover{
  border-color: rgb(185, 121, 1);
}

