.films-card-preview__list{
    display: grid;
    grid-template-columns: 30% 30% 30%;
    column-gap: 3%;
}

.films-card-preview__image-container{
    margin-bottom: 2rem;
}

.films-card-preview__image{
    max-height: 30rem;
    border-radius: 1rem;
}

.films-card-preview__item{
    text-align: center;
    padding: 1.5rem;
    border: 3px solid #1889ca;
    border-radius: 2rem;
}

.films-card-preview__name{
    margin-bottom: 1rem;
}

.films-card-preview__country{
    margin-bottom: 0.5rem;
}

.films-card-preview__genre{
    margin-bottom: 0.5rem;
}

@media screen and (max-width: 1280px){
    .films-card-preview__list{
        display: block;
    }

    .films-card-preview__item{
        margin-bottom: 2rem;
    }

}