.books__list{
    border: 4px solid #1889ca;
    width: 95%;
    margin: 0 auto;
    border-radius: 2rem;
}

.books__list-item{
    display: grid;
    grid-template-columns: 5% 25% 25% 25% 20%;
    grid-template-rows: auto;
}

.book__id{
    grid-area: id;
}

.book__name{
    grid-area: name;
}

.book__author{
    grid-area: author;
}

.book__genre{
    grid-area: genre;
}
.book__year{
    grid-area: year;
}

@media screen and (max-width: 1280px){
    .books__list-item{
    grid-template-columns: 10% 40% 50%;
    grid-template-rows: auto;
    grid-template-areas: 
    "id name genre"
    "id author year"
  ;
    }
    .book__cell:nth-child(4){
        border-right: 0;
    }
}

.book__cell{ 
    padding: 1rem;
    border-right: 4px solid #1889ca;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-bottom: 4px solid #1889ca;
}

.book__cell:nth-child(5){
    border-right: 0;
}

.books__list-item:last-child .book__cell{
    border-bottom: 0;
}







