.about-me{
    display: flex;
    flex-direction: row;
}


.about-me__left-side{
    width: 75%;
    margin-right: 5%;
}

.about-me__right-side{
    width: 20% 
}

@media screen and (max-width: 1280px){

    .about-me{
        flex-direction: row-reverse;
        flex-wrap: wrap;
    }

    .about-me__left-side{
        width: 100%;
        margin: 0 auto;
    }
    
    .about-me__right-side{
        width: 100%;
        margin: 0 auto;
    }

}

.about-me__avatar{
    text-align: center;
    margin-bottom: 1.5rem;
}

.about-me__avatar-image{
    border-radius: 50%;
    border: 3px solid #1889ca;
}

.about-me__fio{
    font-size: 3rem;
    text-align: center;
    margin-bottom: 2rem;
}

.about-me__basic-info-list, .about-me__contacts-list{
    margin-bottom: 1rem;
}

.about-me__title{
    text-align: center;
    margin-bottom: 1rem;
}

.about-me__basic-info-item, .about-me__contacts-item{
    margin-bottom: 1rem;
}

.about-me__basic-info-item:last-child, .about-me__contacts-item:last-child{
    margin-bottom: 0;
}

@media screen and (max-width: 1280px){
    .about-me__basic-info-item, .about-me__contacts-item{
        text-align: center;

    }
}

